* {
  font-family: Helvetica, Arial, sans-serif;
}

body{
  padding: 60px;
}

input {
  color: gray;
  font-size: 1.2rem;
  width: 100%;
}

.grid {
  display: flex;
  flex-wrap: wrap;
}
.grid ol{
  display: contents;
}

.GifCard {
  align-content: center;
  border: 1px solid gray;
  border-radius: 6px;
  overflow: hidden;
  margin-bottom: 10px;
  margin-right: 10px;
}

.GifCard p{
  padding: 5px;
  text-align: center;
}

.GifCard img{
  max-height: 170px;
  width: 100%;
}



